* {
   margin: 0;
   padding: 0;
}

.content {
   min-height: calc(100vh - 70px - -64px);
}

.basket-list {
   position: fixed;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   background-color: #fff;
   width: 50rem;
   max-width: 95%;
   box-shadow: 1px 3px 8px 5px #7c7c7c;
   animation: show 500ms ease-in-out;
}

@keyframes show {
   from {
      top: 70%;
      opacity: 0;
   }

   to {
      top: 50%;
      opacity: 1;
   }
}

.m {
   align-items: center;
   justify-content: center;
}

.prel {
   position: absolute;
   margin-top: 40vh;
}

.cart {
   position: fixed;
   bottom: 2rem;
   right: 2rem;
   cursor: pointer;
   border-radius: 5%;
   z-index: 5;
   padding: 1rem;
   overflow: hidden;
}

@media (min-width: 767px) {
   .cart {
      bottom: unset;
      top: 5rem;
   }
}

.material-items {
   font-size: 35px;
}

.goods {
   display: grid;
   grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
   gap: 1rem;
}

.basket-clear {
   cursor: pointer;
   position: absolute;
   top: 0.5rem;
   right: 0.5rem;
   color: #fff;
}

.basket-quantity {
   cursor: pointer;
   vertical-align: middle;
   color: #7c7c7c;
   font-size: 20px;
}



#toast-container {
   top: 3rem !important;
   animation: fade-it 250ms ease-in-out;
   -webkit-animation: fade-it 250ms ease-in-out;
}

@keyframes fade-in {
   from {
      transform: translateY(2rem);
      opacity: 0;
      -webkit-transform: translateY(2rem);
      -moz-transform: translateY(2rem);
      -ms-transform: translateY(2rem);
      -o-transform: translateY(2rem);
   }

   to {
      transform: translateY(0rem);
      opacity: 1;
      -webkit-transform: translateY(0rem);
      -moz-transform: translateY(0rem);
      -ms-transform: translateY(0rem);
      -o-transform: translateY(0rem);
   }
}